.search {
  box-sizing: border-box;
  width: 100%;
  margin-top: 20px;
  background-color: white;
  border-color: #4caf50;
  border-style: solid;
  border-width: 3px;
  border-radius: 6px;
  padding: 12px;
  font-size: 18px;
  outline: none;
}
@media (max-width: 600px) {
  .search {
    padding: 6px;
    font-size: 12px;
  }
}
