.calendarItem-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
}
.calendarItem {
  display: flex;
  flex-direction: column;
  padding: 15px;
  background-color: #e8f5e9;
  border-radius: 6px;
}
.calendarItem-name {
  color: #70757a;
  margin-bottom: 20px;
}
.calendarItem-score {
  min-width: 31px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight: 500;
}
.winner {
  font-weight: 700;
}
.arrowContainer {
  height: 20px;
  width: 20px;
}
.separatorTeam {
  width: 100%;
  height: 1px;
  background: radial-gradient(#70757a, #70757a00);
  margin: 7px auto;
}
.team-container {
  display: flex;
  justify-content: space-between;
}
.calendarItem-info {
  flex-grow: 1;
  margin-right: 30px;
}
.calendarItem-data {
  text-align: center;
  padding: 0 20px;
}
@media (max-width: 600px) {
  .calendarItem-info {
    margin-right: 5px;
  }
  .calendarItem {
    padding: 10px;
  }
}
