.calendarList {
  margin: 20px auto;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 5px;
}
.calendarList-title {
  margin: 20px 0 30px;
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 26px;
}
.calendarList-error {
  text-align: center;
  font-weight: bold;
  font-size: 26px;
  color: #1b5e20;
}
.calendarList-errorImg {
  display: block;
  height: 200px;
  margin: 20px auto;
}
.calendarList-errorBack {
  width: 250px;
  display: block;
  padding: 10px;
  margin: 10px auto;
  text-align: center;
  font-size: 20px;
  background-color: #e8f5e9;
  border-radius: 6px;
  color: #1b5e20;
  transition: 0.3s;
  text-decoration: none;
}
.calendarList-errorBack:hover {
  background-color: #c8e6c9;
  color: #1b5e20;
}
@media (max-width: 600px) {
  .calendarList-error {
    font-size: 16px;
  }
  .calendarList-errorBack {
    font-size: 16px;
  }
  .calendarList-errorImg {
    width: 150px;
  }
  .calendarList-title {
    font-size: 16px;
  }
}
